<template>
  <div class="container"></div>
</template>
<script>
export default {
  name: 'componentOne',
  data() {
    return {}
  },
  computed: {
    addAnimation() {
      if (
        this.$store.state.hasAnimated.name == 'touch0' &&
        (this.$route.query.finished || this.$store.state.hasAnimated.hasAnimats)
      ) {
        this.$store.commit('showAnimation', {
          hasAnimats: true,
          name: 'touch0'
        })
        return true
      } else {
        return false
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.container {
  background: url('./images/one.jpg') no-repeat;
  background-size: 100% 100%;
}
</style>



