<template>
  <div class="container">
    <van-form @submit="onSubmit" class="form">
      <van-field
        v-model="username"
        name="姓名"
        label="姓名"
        placeholder="姓名"
        :rules="[{ required: true, message: '请填写姓名' }]"
      />
      <van-field
        v-model="company"
        name="公司名称"
        label="公司名称"
        placeholder="公司名称"
        :rules="[{ required: true, message: '请填写公司名称' }]"
      />
      <van-field
        v-model="phone"
        name="联系电话"
        label="联系电话"
        placeholder="联系电话"
        :rules="[
          { required: true, message: '请填写正确的联系电话', validator }
        ]"
      />
      <div style="margin: 16px">
        <van-button color="#e33934" round block type="info" native-type="submit"
          >提交</van-button
        >
      </div>
    </van-form>
  </div>
</template>
<script>
import { add } from '../api/index'
export default {
  name: 'componentFour',
  data() {
    return {
      username: '',
      company: '',
      phone: ''
    }
  },
  methods: {
    validator(val) {
      const telStr = /^[1](([3][0-9])|([4][0,1,4-9])|([5][0-3,5-9])|([6][2,5,6,7])|([7][0-8])|([8][0-9])|([9][0-3,5-9]))[0-9]{8}$/
      if (!telStr.test(val)) {
        return false
      }
      return true
    },
    async onSubmit() {
      const nowDate = new Date()
      const endDate = new Date('2021-04-28')
      if (nowDate >= endDate) {
        this.toast('活动已结束')
      }
      const data = {
        theme: '创业成功锦囊妙计线上沙龙活动',
        createTime: new Date(),
        startDate: '2021-4-28 19:30-21:00',
        participantName: this.username,
        participantCompanyName: this.company,
        participantPhoneNm: this.phone
      }
      const res = await add(data)
      this.toast(res.data.message)
    }
  },
  mounted() {}
}
</script>
<style lang="scss" scoped>
.container {
  background: url('./images/four.jpg') no-repeat;
  background-size: 100% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .form {
    margin-top: 140px;
    width: 70%;
  }
}
</style>



