<template>
  <div class="container"></div>
</template>
<script>
export default {
  name: 'componentThree',
  computed: {
    addAnimation() {
      if (
        this.$store.state.hasAnimated.name == 'touch2' &&
        this.$store.state.hasAnimated.hasAnimats
      ) {
        return true
      } else {
        return false
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.container {
  background: url('./images/three.jpg') no-repeat;
  background-size: 100% 100%;
}
</style>



